
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}
h3, h2 {
  margin-bottom: 5px;
  margin-top: 5px;
}
.refresh:hover {
  color: #F80;
  cursor: pointer;
}
body {
  background: #FCFCFC;
}


.main-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.survey, .result {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: 20px;
}
.button {
  margin: 5px;
  padding: 3px;
  background: #ddd;
  border: 1px solid #444;
  border-radius: 4px;
  font-weight: 600;
}
.button:hover {
  background: #eee;
  cursor: pointer;
}
svg {
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  border-radius: 8px;
}
.player {
  width: 30;
  height: 30;
  cursor: -webkit-grab;
  cursor: grab;
}
